(function(){
    document.addEventListener('DOMContentLoaded', function() {
        filtrarLeidos();
    });

    //Seleccionamos el select de filtros
    const selectFiltros = document.querySelector('#leidos');

    function filtrarLeidos(){
        //Escuchamos la opci√≥n seleccionada
        selectFiltros.addEventListener('change', function(){
            const filtroSeleccionado = this.value;
            if(filtroSeleccionado){
                window.location = `?estado=${filtroSeleccionado}`;
            } else{
                window.location.href = '/admin/inbox';
            }
        });
    } 
})();